import { Component, input, output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { IconFieldModule } from 'primeng/iconfield';
import { InputIconModule } from 'primeng/inputicon';
import { InputTextModule } from 'primeng/inputtext';
import { SelectModule } from 'primeng/select';
import { ButtonModule } from 'primeng/button';

@Component({
  selector: 'app-filter-bar',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    IconFieldModule,
    InputIconModule,
    InputTextModule,
    SelectModule,
    ButtonModule,
  ],
  templateUrl: './app-filter-bar.component.html',
  styleUrl: './app-filter-bar.component.scss',
})
export class AppFilterBarComponent {
  readonly categories = input<string[]>([]);
  readonly sortOptions = input<{ label: string; value: string }[]>([]);
  readonly statusOptions = input<{ label: string; value: string }[]>([]);

  readonly selectedCategory = input<string>('All');
  readonly selectedSort = input<string>('popular');
  readonly selectedStatus = input<string>('ALL');
  readonly searchQuery = input<string>('');

  readonly searchQueryChange = output<string>();
  readonly categoryChange = output<string>();
  readonly sortChange = output<string>();
  readonly statusChange = output<string>();

  onSearch(query: string): void {
    this.searchQueryChange.emit(query);
  }

  onCategoryChange(category: string): void {
    this.categoryChange.emit(category);
  }

  onSortChange(sort: string): void {
    this.sortChange.emit(sort);
  }

  onStatusChange(status: string): void {
    this.statusChange.emit(status);
  }
}
